.imgPreview {
  text-align: center;
  margin: 5px 15px;
  height: 20px;
  width: 500px;
  border-left: 1px solid gray;
  border-right: 1px solid gray;
  border-top: 5px solid gray;
  border-bottom: 5px solid gray;
  .img {
    width: 100%;
    height: 100%;
  }
}

.fileInput {
  border-bottom: 4px solid lightgray;
  border-right: 4px solid black;
  border-top: 1px;
  border-left: 1px solid black;
  padding: 10px;
  margin: 15px;
  cursor: pointer
}

.previewText {
  width: 100%;
  margin-top: 20px;
}

.submitButton {
  padding: 12px;
  margin-left: 10px;
  background: white;
  border: 4px solid lightgray;
  border-radius: 15px;
  font-weight: 700;
  font-size: 10pt;
  cursor: pointer;
  &:hover {
    background: webkit-linear-gradient (top, #f9f9f9, #e3e3e3);
  }
}


